body {
  font-family: "ZCOOL XiaoWei", serif;
  overflow-x: hidden;
}

/* global styles */

.btn-pink {
  color: salmon !important;
  border-color: salmon !important;
  transition: all 500ms ease;
}

.btn-black {
  color: black !important;
  border-color: black !important;
  transition: all 500ms ease;
}

.btn-black:hover {
  color: salmon !important;
  background: black !important;
}

.btn-pink:hover {
  color: black !important;
  background: salmon !important;
}

header {
  overflow: hidden;
  min-height: 100vh;
}

/* nav links */

.navbar {
  z-index: 10;
  position: fixed;
  background: #fff;
  width: 100%;
  top: 0;
}

.navbar-toggler {
  outline: none !important;
}

.navbar-toggler-icon {
  font-size: 1.5rem;
  color: salmon;
}

.nav-link {
  display: inline-block;
  color: salmon;
  transition: 500ms ease;
  font-size: 1.5rem;
}

.nav-link:hover {
  color: black;
}

/* cart */

.cart-info__icon {
  font-size: 1.3rem;
  color: black;
  cursor: pointer;
}

.cart-info {
  max-height: 60px;
  border: 0.1rem solid black;
  border-radius: 0.25rem;
  padding: 0.5rem 0.6rem;
  cursor: pointer;
  transition: 500ms ease;
}

.cart-info:hover {
  background: salmon;
  border-color: salmon;
  color: #fff;
}

.cart-info:hover .cart-info__icon {
  color: white;
}

.max-height {
  min-height: 100vh;
  background: linear-gradient(rgb(250, 239, 143), rgba(238, 234, 202, 0.472)),
    url(../images/cakes-chocolate-close-up-959079.jpg);
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.banner {
  color: #fff;
  margin-top: -2rem;
}

.banner-title {
  font-size: 3.5rem;
  color: salmon;
}

.banner-link {
  font-size: 1.5rem;
  color: black;
  border: 0.2rem solid black;
}

.banner-link:hover {
  background: black;
  color: salmon;
}

/* ABOUT */

.about-img__container {
  position: relative;
}

.about-img__container::before {
  content: "";
  position: absolute;
  top: -1.5rem;
  left: -1.7rem;
  width: 100%;
  height: 100%;
  outline: 0.5rem solid sandybrown;
  z-index: -1;
  /* esta por detras da imagem */
  transition: all 500ms ease;
}

.about-img__container:hover:before {
  top: 0;
  left: 0;
}

/* store */

.store {
  background: #ccc;
}

.store .img-container img {
  min-height: 250px;
  max-height: 250px;
}

.search-box {
  background: salmon;
  color: black;
}

.single-item {
  transition: all 0.3 linear;
}

.single-item:hover {
  box-shadow: 0 10px 15px rgb(0, 0, 0);
}

.img-container {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.store-img {
  transition: all 800ms ease-in-out;
}

.img-container:hover .store-img {
  transform: scale(1.4);
}

.store-item-icon {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 0.75rem;
  background: rgb(190, 240, 89);
  border-top-left-radius: 1rem;
  transition: all 600ms ease-in-out;
  transform: translate(100%, 100%);
}

.img-container:hover .store-item-icon {
  transform: translate(0, 0);
}

.store-item-icon:hover {
  color: #fff;
}

/* services */

.services {
  color: #fff;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(../images/assorted-baked-brownies-352779.jpg) center/cover fixed no-repeat;
}

.service-title {
  letter-spacing: 0.5rem;
}

.service-text {
  word-spacing: 0.3rem;
}

/* footer */

.footer-title {
  background: #000;
}

.footer-title h1 {
  margin-bottom: 2rem;
}

.footer-contact {
  background: #ccc;
  padding-left: 5.5rem !important;
}

.footer-icon {
  font-size: 2rem;
  color: #fff;
  transition: all 500ms ease;
}

.footer-icon:hover {
  color: salmon;
  cursor: pointer;
}

footer .container-fluid span {
  font-size: 1.6rem;
}


/* cart */

.cart {
  position: fixed;
  min-height: 12rem;
  background: rgba(230, 224, 224, 0.685);
  width: 18rem !important;
  transform: translate(100%, 0);
  top: 86px;
  right: 0;
  transition: all 1s ease-in-out;
  overflow: hidden;
  z-index: 10;
}

#clear-cart {
  font-size: 1.5rem;
  padding-top: 0.5rem;
}

.show-cart {
  padding: 2rem 1.5rem;
  transform: translate(0, 0);
}

.cart-item {
  transition: all 2s ease-in-out;
}

.cart-item-remove {
  color: salmon;
  transition: all 500ms ease-in-out;
}

.cart-item-remove:hover {
  transform: scale(1.1);
  color: black;
}

.cart-item-price {
  font-size: 0.8rem;
}

#map {

  width: 100%;

  min-height: 350px;
}


/* MEDIA QUERIES */

@media screen and (max-width: 991px) {}

@media screen and (max-width: 770px) {}


@media screen and (max-width: 560px) {

  .footer-contact {
    background: #ccc;
    padding: 2rem !important;
  }
}

@media screen and (max-width: 489px) {


  footer span {
    font-size: 1.2rem !important;
  }

  footer .footer-icon {
    font-size: 1.6rem !important;
  }
}